import React from 'react';
import s from './Friends.module.scss';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';

const Friends = (props) => {
    const friendsData = props.friends;

    return (
        <section className={s.wrapper}>
            <h1>Friends</h1>
            <div className={s.list}>
                {friendsData.map(friend => {
                    return (
                        <Link to={`/friends/${friend.id}`} className={s.item} key={friend.id}>
                            <img className={s.itemPic} src={friend.pic} alt=""/>
                            <div className={s.itemName}>{friend.name}</div>
                        </Link>
                    );
                })}
            </div>
        </section>
    );
};

const mapStateToProps = (state) => {
    return {
        friends: state.sidebar.friends
    }
};

export default connect(mapStateToProps, null)(Friends);